.promo {
  padding-top: 149px;
  padding-bottom: 60px;

  @media (--middle-vp) {
    padding-top: 92px;
    padding-bottom: 50px;
  }

  @media (--small-vp) {
    padding-top: 62px;
    padding-bottom: 35px;
  }
}

.promo h2 {
  text-align: center;
  margin-bottom: 34px;

  @media (--middle-vp) {
    margin-bottom: 41px;
    padding-right: 9px;
  }

  @media (--small-vp) {
    margin-bottom: 43px;
    padding-right: 5px;
  }
}

.promo__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;

  @media (--not-wide-vp) {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.promo__main {
  display: flex;
  width: 100%;

  @media (--small-vp) {
    flex-direction: column;
  }
}

.promo__main-container {
  display: flex;
  flex-direction: column;
  background-color: var(--color-bg-action);
  width: 50%;
  padding: 33px 45px;

  @media (--middle-vp) {
    width: 41.2%;
  }

  @media (--small-vp) {
    width: 100%;
    text-align: center;
    padding-bottom: 46px;
  }
}

.promo__image {
  width: 50%;

  @media (--middle-vp) {
    width: 58.8%;
    height: 462px;
  }

  @media (--small-vp) {
    width: 100%;
    height: 299px;
  }
}

.promo__main img {
  background-color: var(--color-bg-promo-img);
  display: block;

  @media (--not-wide-vp) {
    object-position: top;
  }
}

.promo__cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
  margin: 0;

  @media (--middle-vp) {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }

  @media (--small-vp) {
    gap: 30px;
  }
}

.promo__cards-item {
  display: flex;
  flex-direction: column;
  border: solid 7px transparent;
  border-image: var(--linear-gradient-border-cards);
  border-image-slice: 1;
  padding: 17px 28px;

  @media (--middle-vp) {
    min-height: 211px;
  }

  @media (--small-vp) {
    text-align: center;
    padding-top: 29px;
    padding-bottom: 40px;
  }
}

.promo__main-text h3 {
  font-size: 28px;
  line-height: 156%;
  margin-top: 0;
  margin-bottom: 0;
}

.promo__main-text span {
  display: block;
  font-weight: 700;
  font-size: 70px;
  line-height: 100%;
  margin-top: -5px;
  margin-bottom: 18px;

  @media (--small-vp) {
    margin-bottom: 14px;
  }
}

.promo__main-text p {
  font-size: 18px;
  line-height: 140%;
  margin-top: 0;

  @media (--small-vp) {
    margin-bottom: 31px;
  }
}

.promo__main-container a {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-base-white);
  text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 3px;
  margin-top: auto;
  margin-bottom: 14px;
  width: fit-content;
  user-select: none;

  @media (--small-vp) {
    margin: 0 auto;
  }

  &:focus {
    outline: 1px solid var(--color-base-white);
    outline-offset: 8px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 1px solid var(--color-base-white);
    outline-offset: 8px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}

.promo__card-text {
  color: var(--color-text-card-promo);
}

.promo__card-text h3 {
  font-size: 26px;
  line-height: 150%;
  margin-top: 0;
  margin-bottom: 3px;
}

.promo__card-text p {
  font-size: 16px;
  line-height: 140%;
  margin-top: 0;

  @media (--small-vp) {
    margin-bottom: 27px;
  }
}

.promo__cards-item a {
  display: block;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--color-text-card-promo);
  text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 4px;
  margin-top: auto;
  margin-bottom: 14px;
  width: fit-content;
  user-select: none;

  @media (--small-vp) {
    margin: 0 auto;
  }

  &:focus {
    outline: 1px solid var(--color-text-card-promo);
    outline-offset: 8px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 1px solid var(--color-text-card-promo);
    outline-offset: 8px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}
